.wrapper {
    display: flex;
    border-collapse: collapse;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 100%;
    height: 100vh;
}
.allTable {
    border-collapse: collapse;

    width: 1008px;

    text-align: center;
}
.tableHead {
    height: 48px;

    background-color: #EFEFEF;
}
.tableRow {
    height: 48px;

    border:  1px solid #D9D9D9;
}
.tableData {
    max-width: 300px;
}